.newsItem {
    width: 350px;
    height: 300px;
    background-color: rgba(0, 149, 186, 0.1);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    cursor: pointer;
}
.newsItem:hover {
    scale: 1.1;
}
.newsItem img {
    width: 100%;
    padding-left: 12px;
    padding-right: 12px;
    padding-top: 12px;
    height: 200px;
}
.newsItem h1 {
    margin-top: 10px;
    font-weight: 700;
    font-size: 17px;
    width: 100%;
    padding-left: 12px;
    padding-right: 12px;
}
.newsItem p {
    padding-left: 12px;
    padding-right: 12px;
    margin-top: 10px;
    color: grey;
}
.news {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    inset: 10px;
    max-width: 1280px;
    margin: 0 auto;
}
.news_title {
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    margin-top: 30px;
    margin-bottom: 30px;
    color: rgb(0, 143, 179);
}
.pagination {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.pagitation_button {
    width: 50px;
    height: 50px;
    margin: 10px;
    background-color: rgb(218, 218, 218);
    border-radius: 10px;
}
.category {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
}
.category_item {
    padding: 12px;
    margin: 20px;
    background-color: rgb(157, 157, 157);
    border-radius: 5px;
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 10px 4px 12px -2px rgba(0,0,0,0); 
box-shadow: 0px 10px 13px -7px #000000, 10px 4px 12px -2px rgba(0,0,0,0);
}
.category_item:hover {
    scale: 1.1;
}
.news_form {
    width: 405px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
}
.news_form input {
    width: 300px;
    height: 50px;
    background-color: rgb(0, 87, 192);
    border-radius: 10px;
    padding-left: 10px;
    color: white;
    font-size: 17px;
}
.news_form input::placeholder {
    color: white;
    padding-left: 0px;
    font-size: 17px;
}
.news_form input:focus {
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 4px 10px 14px -5px rgba(0,0,0,0.22); 
box-shadow: 0px 10px 13px -7px #000000, 4px 10px 14px -5px rgba(0,0,0,0.22);
}
.news_form button {
    margin-left: 20px;
    padding: 10px 20px;
    background-color: rgb(0, 149, 255);
    border-radius: 5px;
}
.news_form button:hover {
    scale: 1.1;
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 4px 10px 14px -5px rgba(0,0,0,0.22); 
box-shadow: 0px 10px 13px -7px #000000, 4px 10px 14px -5px rgba(0,0,0,0.22);
}